#root {
  --main-darklg: #1d2635;
  --main-dark: #f7fafd;
  --primary-color: #3f50b5;
  --main-light: #eeeeee;
  background-color:#191d25 !important;  /* #3d424e;*/
  font-family: "Poppins", sans-serif;
}

* {
  margin: 0;
  padding: 0;
  
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 8vh;
  position: relative;
  width: 100%;
  background-color: var(--main-darklg);
}


.main {
  overflow: hidden;
  height: 92vh;
  display: flex;
 
}

.main__left {
  flex: 0.7;
  display: flex;
  flex-direction: column;
}

.videos__group {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  background-color: var(--main-dark);
}

video {
  border-style: outset;
  border-color: black;
  height: 325px;
  border-radius: 1rem;
  margin: 0.2rem;
  width: 400px;
  object-fit: fill;
  transform: rotateY(180deg);
  -webkit-transform: rotateY(180deg);
  -moz-transform: rotateY(180deg);
}
/* DONT FORGET TO CHANGE THE VIDEO MIN MAX DIMENSION FOR SCREEN SHARE*/

#screen-share {
  height: 600px;
  width: 1200px;
  min-height: 325;
  min-width: 400px;
  border-radius: 1rem;
  margin: 0.2rem;
 
  object-fit: fill;
  justify-content: center;
  transform: rotateY(360deg);
  -webkit-transform: rotateY(360deg);
  -moz-transform: rotateY(360deg);
}


.background__red {
 
  background-color: grey;
}

.main__right {
  display: flex;
  flex-direction: column;
  flex: 0.3;
  background-color: #242f41;
}



#video-grid {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}



.header__back {
  display: none;
  position: absolute;
  font-size: 1.3rem;
  top: 17px;
  left: 28px;
  color: #fff;
}



/*for app bar*/

.footbar-wrapper {
    display: flex;
    align-items: center;
   justify-content: center;
    padding: 0;
    top: unset;
    bottom: 0;
    position: fixed;
    background-color: white;
    min-width: 200px;
    max-width: 100%;
    max-height: 20%;
    min-height: 15px;
   
    
}

.footbar-tool {
  width: 20%;
  height: 10%;
  padding: 0;
  display: flex;
  justify-content: space-around;
  
}

/* For chat*/
.rcw-launcher{
  box-shadow: none !important;
  margin: 0 !important;
  background-color: #cbd3fe52 !important;
}


.rcw-widget-container .rwc-launcher{
 
  box-shadow: none !important;
}
.rcw-conversation-container{
  box-shadow: none !important;
  
}
.rcw-conversation-container .rcw-header{
  background-color:var(--primary-color) !important;
  border-bottom-color: white;
}

.rcw-conversation-container .rcw-title{
  padding: 0 !important;
}

.rcw-conversation-container .rcw-messages-container .rcw-message .chat-messages{
  float: left;
  width: 80%;
  margin-bottom: 20px;
}



.rcw-conversation-container .rcw-messages-container .rcw-message .message{
  float: left;
  padding: 15px;
  background: #f4f7f9 !important;
  font-size: 15px;
  border-radius: 10px;
  margin-bottom: 3px;
}
.rcw-conversation-container .rcw-messages-container .rcw-message .from{
  float: left;
  display: block;
  width: 100%;
  text-align: left;
  font-size: 9px;
}

.rcw-conversation-container .rcw-messages-container .rcw-message .new-user{
  float: center;
  display: block;
  width: 100%;
  text-align: center;
  font-size: 11px;
}



@media (max-width: 700px) {
  .main__right {
    display: none;
  }
  .main__left {
    width: 100%;
    flex: 1;
  }

  video {
    height: auto;
    width: 100%;
  }

}

