
body, html {
    font-family: 'Roboto', sans-serif;
    height: 100%;
    width: 100%;
    margin: 0;
    background-color:#191d25 !important;
}

#root{
  font-family: 'Roboto', sans-serif;
  height: 100% !important;
  width: 100% !important;
  margin: 0;
  background-color:#191d25 !important;
}

  .outer {
    display: table;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
  }
  
  .middle {
    display: table-cell;
    vertical-align: middle;
  }
  
  .inner {
    margin-left: auto;
    margin-right: auto;
    width: 500px;
    height: 300px;
    /* Whatever width you want */
  }


.center-card{
    align-items: center;
    width: 20% !important;
}





  