}

  .outer {
    display: table;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
  }
  
  .middle {
    display: table-cell;
    vertical-align: middle;
  }
  
  .inner {
    margin-left: auto;
    margin-right: auto;
    width: 500px;
    height: 300px;
    /* Whatever width you want */
  }


.center-card{
    -webkit-align-items: center;
            align-items: center;
